@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');
*{
   padding: 0;
   margin: 0;
   font-family: 'Poppins', sans-serif;
}
.main{
   height: 100vh;
   width: 100vw;
   display: flex;
   align-items: center;
   justify-content: center;
   background-image: url(https://png.pngtree.com/background/20210712/original/pngtree-blue-abstract-background-picture-image_1170267.jpg);
   background-position: center;
   background-size: cover;
}
.container{
   background-color: var(--secondary-color);
   padding: 50px;
   border-radius: 32px;
   box-shadow: var(--shadow-color) 0px 3px 8px;
   display: flex;
   width: 80%;
}
/* Right image */
.container .right{
   display: flex;
   justify-content: center;
   align-items: center;
   width: 50%;
}
.container .right img{
   width: 100%;
   margin-left: 10px;
}